.bd-websites {
  --gap: 0px;
  --width: 672px;
  --spacing: 1.5rem;
  display: grid;
  grid-gap: var(--gap);
  grid-template-columns: 1fr;
  margin-left: auto;
  margin-right: auto;
  max-width: calc( calc(2 * calc(var(--width) + var(--spacing)) + calc(3 * var(--gap))));
  padding-left: var(--spacing);
  padding-right: var(--spacing);
}

.bd-website {
  display: block;
  margin-left: auto;
  margin-right: auto;
  max-width: calc(2 * var(--width));
  padding-bottom: var(--spacing);
  position: relative;
  text-align: center;
  overflow: hidden;
  position: relative;
}

.bd-website:hover {
  background-color: var(--expo-light);
}

.bd-website:hover .title {
  color: var(--expo-dark);
}

.bd-website:hover .bd-website-date {
  color: #363636;
}

.bd-website:hover .bd-website-shadow {
  opacity: 0;
}

.bd-website:hover .bd-website-overlay {
  background-color: rgba(10, 10, 10, 0.1);
}

.bd-website.bd-is-highlighted {
  grid-column: span 2;
}

.bd-website-date {
  color: #7a7a7a;
}

.bd-website-image {
  align-items: center;
  display: flex;
  justify-content: center;
  margin-bottom: var(--spacing);
  position: relative;
}

.bd-website-image img {
  border-radius: 0.375em;
}

.bd-website-image .b-lazy {
  opacity: 0;
  transition: opacity 500ms ease-out;
}

.bd-website-image .b-loaded {
  opacity: 1;
}

.bd-website-shadow {
  border: 1px solid rgba(0, 0, 0, 0.04);
  transition: opacity 200ms ease-out;
}

.bd-website-overlay {
  background-color: transparent;
  border-radius: 0.375em;
  box-shadow: inset 0 0 0 1px rgba(0, 0, 0, 0.04);
  transition: background-color 200ms ease-out;
}

@media screen and (min-width: 769px), print {
  .bd-websites {
    --spacing: 3rem;
    grid-template-columns: repeat(auto-fill, minmax(var(--width), 1fr));
  }
}

.bd-backers {
  --spacing: 1.5rem;
  padding-bottom: var(--spacing);
  padding-top: var(--spacing);
}

.bd-backers-platforms {
  --spacing: 1.5rem;
  padding: var(--spacing);
  text-align: center;
}

.bd-backers-platform .bd-backers-platform-logo {
  border-radius: 6px;
  padding: var(--spacing);
  transition-duration: 172ms;
  transition-property: box-shadow;
}

.bd-backers-platform .bd-backers-platform-logo:hover {
  box-shadow: 0 0 0 2px var(--color);
}

.bd-backers-platform.bd-is-patreon {
  --color: var(--patreon);
  grid-column: 2;
}

.bd-backers-platform.bd-is-github {
  --color: var(--github);
  grid-column: 3;
}

.bd-backers-platform .bd-fat-button {
  margin-top: 1em;
}

.bd-backers-heading h2 {
  background-color: var(--sponsor-light);
  border-radius: 6px;
  color: var(--sponsor);
  font-size: 1em;
  font-weight: 600;
  padding: 0.5em 1em;
}

.bd-backers-group {
  padding: var(--spacing);
  text-align: center;
}

.bd-backers-list {
  display: grid;
