:root {
  --newsletter-strip-size: 16px;
  --newsletter-border-size: 1.5rem;
}

#newsletter {
  background: #fafafa;
  padding: var(--newsletter-border-size);
  padding-top: 0;
}

#newsletter .bd-drawing {
  display: none;
}

.bd-newsletter-heading > .icon {
  font-size: 2rem;
  height: 1.125em;
  width: 1.125em;
}

.bd-newsletter-heading .title,
.bd-newsletter-heading .subtitle {
  font-weight: 400;
}

.bd-newsletter-heading .title strong,
.bd-newsletter-heading .subtitle strong {
  color: inherit;
  font-weight: 600;
}

.bd-newsletter-heading .title small,
.bd-newsletter-heading .subtitle small {
  color: #7a7a7a;
  display: block;
  font-size: 0.75em;
}

.bd-newsletter-heading .subtitle {
  color: #7a7a7a;
}

.bd-newsletter-heading .bd-fat-button {
  font-weight: 400;
}

.bd-newsletter-heading .bd-fat-button strong {
  font-weight: 600;
}

.bd-newsletter-fields {
  display: grid;
  grid-gap: 0.75rem;
  grid-template-columns: minmax(20em, 1fr) auto;
}

.bd-newsletter-box {
  background-color: white;
  background-image: url("/images/hab/newsletter-strip.png");
  background-repeat: repeat-x;
  background-size: 96px var(--newsletter-strip-size);
  border-radius: 1.5em;
  box-shadow: 0 0 0 1px whitesmoke;
}

.bd-newsletter-box .icon {
  align-self: flex-start;
}

.bd-newsletter-box form .input:not(:focus):not(:hover) {
  border-color: #dbdbdb;
}

@media screen and (max-width: 768px) {
  .bd-newsletter-box {
    padding-top: calc(1.5rem + var(--newsletter-strip-size));
  }
  .bd-newsletter-fields {
    grid-template-columns: repeat(auto-fill, minmax(14em, 1fr));
  }
}

@media screen and (max-width: 1023px) {
  .bd-newsletter-heading {
    margin-bottom: var(--bd-index-vertical);
  }
  .bd-newsletter-heading .icon {
    float: left;
    margin-right: 0.75rem;
  }
  .bd-newsletter-heading .subtitle {
    margin: 0.75rem 0 !important;
  }
  .bd-newsletter-heading form {
    margin-top: 1.5rem;
  }
}

@media screen and (min-width: 769px), print {
  .bd-newsletter-box {
    padding-top: calc(var(--bd-index-vertical) + var(--newsletter-strip-size));
  }
}

@media screen and (min-width: 1024px) {
  .bd-newsletter-heading {
    display: grid;
    grid-template-columns: 3rem 3rem 1fr 1fr 3rem 3rem;
  }
  .bd-newsletter-heading > .icon {
    grid-column: 2;
  }
  .bd-newsletter-heading .title {
    grid-column: 3;
    max-width: 9em;
  }
  .bd-newsletter-heading .subtitle {
    grid-column: 4;
    max-width: 10em;
  }
  .bd-newsletter-heading form {
    grid-column: 4;
  }
}

@media screen and (min-width: 1216px) {
  :root {
    --newsletter-border-size: 5.625rem;
  }
  #newsletter .bd-drawing {
    display: block;
  }
  .bd-newsletter-heading {
    grid-template-columns: 4.5rem 4.5rem 1fr 1fr 4.5rem 4.5rem;
  }
  .bd-newsletter-box {
    box-shadow: 0 1em 2em rgba(0, 0, 0, 0.05);
  }
}

@media screen and (min-width: 1780px) {
  .bd-newsletter-heading {
    grid-template-columns: minmax(3rem, 1fr) 4.5rem calc(var(--container-width) / 2) calc(var(--container-width) / 2) 4.5rem minmax(3rem, 1fr);
  }
}

.cpck-window {
