.bulma-arrow-mixin {
  border: 3px solid purple;
  border-radius: 2px;
  border-right: 0;
  border-top: 0;
  content: " ";
  display: block;
  height: 0.625em;
  margin-top: -0.4375em;
  pointer-events: none;
  position: absolute;
  top: 50%;
  transform: rotate(-45deg);
  transform-origin: center;
  width: 0.625em;
}

.bulma-hamburger-mixin {
  -moz-appearance: none;
  -webkit-appearance: none;
  appearance: none;
  background: none;
  border: none;
  cursor: pointer;
  display: block;
  height: 2.5rem;
  position: relative;
  width: 2.5rem;
}

.bulma-hamburger-mixin span {
  background-color: currentColor;
  display: block;
  height: 1px;
  left: calc(50% - 8px);
  position: absolute;
  transform-origin: center;
  transition-duration: 86ms;
  transition-property: background-color, opacity, transform;
  transition-timing-function: ease-out;
  width: 16px;
}

.bulma-hamburger-mixin span:nth-child(1) {
  top: calc(50% - 6px);
}

.bulma-hamburger-mixin span:nth-child(2) {
  top: calc(50% - 1px);
}

.bulma-hamburger-mixin span:nth-child(3) {
  top: calc(50% + 4px);
}

.bulma-hamburger-mixin:hover {
  background-color: rgba(0, 0, 0, 0.05);
}

.bulma-hamburger-mixin.is-active span:nth-child(1) {
  transform: translateY(5px) rotate(45deg);
}

.bulma-hamburger-mixin.is-active span:nth-child(2) {
  opacity: 0;
}

.bulma-hamburger-mixin.is-active span:nth-child(3) {
  transform: translateY(-5px) rotate(-45deg);
}

.bulma-delete-mixin {
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -moz-appearance: none;
  -webkit-appearance: none;
  background-color: rgba(10, 10, 10, 0.2);
  border: none;
  border-radius: 9999px;
  cursor: pointer;
  pointer-events: auto;
  display: inline-block;
  flex-grow: 0;
  flex-shrink: 0;
  font-size: 0;
  height: 20px;
  max-height: 20px;
  max-width: 20px;
  min-height: 20px;
  min-width: 20px;
  outline: none;
  position: relative;
  vertical-align: top;
  width: 20px;
}

.bulma-delete-mixin::before, .bulma-delete-mixin::after {
  background-color: white;
  content: "";
  display: block;
  left: 50%;
  position: absolute;
  top: 50%;
  transform: translateX(-50%) translateY(-50%) rotate(45deg);
  transform-origin: center center;
}

.bulma-delete-mixin::before {
  height: 2px;
  width: 50%;
}

.bulma-delete-mixin::after {
  height: 50%;
  width: 2px;
}

.bulma-delete-mixin:hover, .bulma-delete-mixin:focus {
  background-color: rgba(10, 10, 10, 0.3);
}

.bulma-delete-mixin:active {
  background-color: rgba(10, 10, 10, 0.4);
}

.bulma-delete-mixin.is-small {
  height: 16px;
  max-height: 16px;
  max-width: 16px;
  min-height: 16px;
  min-width: 16px;
  width: 16px;
}

.bulma-delete-mixin.is-medium {
  height: 24px;
  max-height: 24px;
  max-width: 24px;
  min-height: 24px;
  min-width: 24px;
  width: 24px;
}

.bulma-delete-mixin.is-large {
  height: 32px;
  max-height: 32px;
  max-width: 32px;
  min-height: 32px;
  min-width: 32px;
  width: 32px;
}

.bulma-loader-mixin {
  animation: spinAround 500ms infinite linear;
  border: 2px solid #dbdbdb;
  border-radius: 9999px;
  border-right-color: transparent;
  border-top-color: transparent;
  content: "";
  display: block;
  height: 1em;
  position: relative;
  width: 1em;
}

.bulma-block-mixin:not(:last-child) {
  margin-bottom: 1rem;
}

.bulma-fa-mixin {
  display: inline-block;
  font-size: 1rem;
  height: 2rem;
  line-height: 2rem;
  text-align: center;
  vertical-align: top;
  width: 2rem;
  background-color: lavender;
}

.bulma-overlay-mixin-parent {
